

.buttonIcon {
    padding: 5px;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    background:#ffffff;

  }

.click {
      background-color: #f8f9fa;
      border: 1px solid #f8f9fa;
      border-radius: 4px;
      color: #3c4043;
      cursor: pointer;
      font-family: 'Poppins', sans-serif;
      font-size: 14px;
      height: 36px;
      line-height: 27px;
      min-width: 54px;
      padding: 0 16px;
      text-align: center;
      user-select: none;
      -webkit-user-select: none;
      touch-action: manipulation;
      white-space: pre;
    }
    
    .click:hover {
      border-color: #dadce0;
      box-shadow: rgba(0, 0, 0, .1) 0 1px 1px;
      color: #202124;
    }
    
    .button-16:focus {
      border-color: #4285f4;
      outline: none;
    }